/* @include single_order_style; */
@mixin single_order_style
{
	$row_bg_color:#e5e5e5;
	$row_bg_color_odd:#f7f7f7;
	$header_bg_color:#bfbfbf;
	$border_color:#777;
	.woob2b-products-list{
		position:relative;
		display:block;
		width:100%;
		height:auto;
		&:after{
			content:"";
			display:table;
			clear:both;
		}
		.item{
			position:relative;
			display:block;
			height:auto;
			min-height:200px;
			float:left;
			
			.inner{
				position:relative;
				display:block;
				height:auto;
				margin:15px;
				width:calc(100% - 30px);
				border:1px solid $border_color;
				a,a:visited,a:active{
					border:none;
					outline:none;
					text-decoration: none;
					
				}
				a:hover{
					border:none;
					outline:none;
					text-decoration: none;
				}
			}
			p{
				text-align:center;
				font-size:16px;
				line-height:21px;
				font-weight:bold;
				padding:4px 0;
			}
		}
	}
	.woob2b-single-order {
		position:relative;
		display:block;
		width:100%;
		height:auto;
		&.admin{
			width:calc(100% - 60px);
			margin-left:30px;
			margin-right:30px;
			margin-top:20px;
			max-width:1100px;
			h1{
				font-size:25px;
				line-height:40px;
				padding-left:15px;
				border-bottom:1px solid $border_color;
			}
			h3{
				font-size:20px;
				line-height:37px;
			}
		}
		h2,h3,h4{
			border-bottom:1px solid $border_color;
			margin-bottom:15px;
			margin-top:7px;
		}
		h2{
			font-size:2rem;
		}
		h3{
			font-size:1.6rem;
		}
		&:after{
			content:"";
			display:table;
			clear:both;
		}
		.woob2b-group{
			position:relative;
			display:block;
			width:100%;
			clear:both;
			margin-bottom:10px;
			margin-bottom:35px;
			.btn{
				display:inline-block;
				padding:4px 25px;
				line-height:30px;
				margin-bottom:8px;
				margin-left:5px;
				margin-right:5px;
				border:1px solid $border_color;
				text-decoration: none;
				&:hover{
					text-decoration: none;
				}

			}
			&.one-col{
				margin-left:-5px;
				margin-right:-5px;
				width:calc(100% + 10px);
			}
		}
		.woob2b-row{
			position:relative;
			display:block;
			width:100%;
			clear:both;
			&:after{
				content:"";
				display:table;
				clear:both;
			}
		}
		.coll{
			position:relative;
			display:block;
			height:auto;
			float:left;
			&.left{
				width:75%;
				.inner{
					position:relative;
					display:block;
					width:100%;
					padding:15px 40px 15px 15px;
				}
			}
			&.right{
				width:25%;
				img{
					width:100%;
					max-width:100%;
					height:auto;
				}
			}
			&.inner-left{
				width:40%;
				margin-bottom:7px;
			}
			&.inner-right{
				width:calc(60% - 20px);
				padding-left:20px;
				margin-bottom:7px;
			}
			&.full{
				width:100%;
				clear:both;
			}
		}
		&.admin{
			.coll{
				&.left{
					width:calc(75% - 30px);
					padding-right:30px;
				}
			}
		}
	}
	/*------------------------------ media ------------------------*/
	@media (max-width: 750px) {
		.woob2b-single-order {
			.coll{
				&.left,&.right{
					width:100% !important;
				}
			}
			
		}
		.woob2b-products-list{
			.item{
				width:50%;
			}
		}
	}
	
	
	/*------------------------ colsizes -----------------------*/
	
	
	
	@media (min-width: 1200px) {
		.woob2b-products-list .item{
			&.col-xl-2{
				width:50%;
			}
			&.col-xl-3{
				width:33.33%;
			}
			&.col-xl-4{
				width:25%;
			}
		}
	}
	@media (max-width: 1200px) {
		.woob2b-products-list .item{
			&.col-xl-2{
				width:50%;
			}
			&.col-xl-3{
				width:33.33%;
			}
			&.col-xl-4{
				width:25%;
			}
		}
	}
	@media (max-width: 992px) {
		.woob2b-products-list .item{
			&.col-lg-1{
				width:100%;
			}
			&.col-lg-2{
				width:50%;
			}
			&.col-lg-3{
				width:33.33%;
			}
			&.col-lg-4{
				width:25%;
			}
		}
	}
	@media (max-width: 768px) {
		.woob2b-single-order .woob2b-group{

			.btn{
				padding:4px 17px;
				line-height:30px;
				font-size:13px;

			}

		}
		.woob2b-products-list .item{
			&.col-md-1{
				width:100%;
			}
			&.col-md-2{
				width:50%;
			}
			&.col-md-3{
				width:33.33%;
			}
			&.col-md-4{
				width:25%;
			}
		}
	}
	@media (max-width: 576px) {
		.woob2b-products-list .item{
			&.col-sm-1{
				width:100%;
			}
			&.col-sm-2{
				width:50%;
			}
		}
	}
	@media (max-width: 420px) {
		.woob2b-products-list .item{

			&.col-sm-2{
				width:100%;
			}
		}
	}
}